import { IInputType } from "./inputType/InputType";
import { Axis } from "./AxisManager";
import { AnimationManager } from "./AnimationManager";
import Axes from "./Axes";
import { roundNumbers } from "./utils";
import { AnimationParam, OnAnimationStart, OnRelease } from "./types";

export interface ChangeEventOption {
	input: IInputType;
	event;
}

export class EventManager {
	public am: AnimationManager;
	constructor(private axes: Axes) {}
	/**
	 * This event is fired when a user holds an element on the screen of the device.
	 * @ko         
	 * @name eg.Axes#hold
	 * @event
	 * @type {object}
	 * @property {Object.<string, number>} pos coordinate <ko> </ko>
	 * @property {Object} input The instance of inputType where the event occurred<ko>  inputType </ko>
	 * @property {Object} inputEvent The event object received from inputType <ko>inputType    </ko>
	 * @property {Boolean} isTrusted Returns true if an event was generated by the user action, or false if it was caused by a script or API call <ko>     true,  API    false .</ko>
	 *
	 * @example
	 * const axes = new eg.Axes({
	 *   "x": {
	 *      range: [0, 100]
	 *   },
	 *   "zoom": {
	 *      range: [50, 30]
	 *   }
	 * }).on("hold", function(event) {
	 *   // event.pos
	 *   // event.input
	 *   // event.inputEvent
	 *   // isTrusted
	 * });
	 */
	triggerHold(pos: Axis, option: ChangeEventOption) {
		const {roundPos} = this.getRoundPos(pos);

		this.axes.trigger("hold", {
			pos: roundPos,
			input: option.input || null,
			inputEvent: option.event || null,
			isTrusted: true,
		});
	}

	/**
	 * Specifies the coordinates to move after the 'change' event. It works when the holding value of the change event is true.
	 * @ko 'change'     . change holding  true  
	 * @name set
   * @function
	 * @param {Object.<string, number>} pos The coordinate to move to <ko> </ko>
	 * @example
	 * const axes = new eg.Axes({
	 *   "x": {
	 *      range: [0, 100]
	 *   },
	 *   "zoom": {
	 *      range: [50, 30]
	 *   }
	 * }).on("change", function(event) {
	 *   event.holding && event.set({x: 10});
	 * });
	 */
	/** Specifies the animation coordinates to move after the 'release' or 'animationStart' events.
	 * @ko 'release'  'animationStart'     .
	 * @name setTo
   * @function
	 * @param {Object.<string, number>} pos The coordinate to move to <ko> </ko>
	 * @param {Number} [duration] Duration of the animation (unit: ms) <ko>  (: ms)</ko>
	 * @example
	 * const axes = new eg.Axes({
	 *   "x": {
	 *      range: [0, 100]
	 *   },
	 *   "zoom": {
	 *      range: [50, 30]
	 *   }
	 * }).on("animationStart", function(event) {
	 *   event.setTo({x: 10}, 2000);
	 * });
	 */
	/**
	 * This event is fired when a user release an element on the screen of the device.
	 * @ko        
	 * @name eg.Axes#release
	 * @event
	 * @type {object}
	 * @property {Object.<string, number>} depaPos The coordinates when releasing an element<ko>    </ko>
	 * @property {Object.<string, number>} destPos The coordinates to move to after releasing an element<ko>    </ko>
	 * @property {Object.<string, number>} delta  The movement variation of coordinate <ko> </ko>
	 * @property {Object} inputEvent The event object received from inputType <ko>inputType    </ko>
	 * @property {Object} input The instance of inputType where the event occurred<ko>  inputType </ko>
	 * @property {setTo} setTo Specifies the animation coordinates to move after the event <ko>     </ko>
	 * @property {Boolean} isTrusted Returns true if an event was generated by the user action, or false if it was caused by a script or API call <ko>     true,  API    false .</ko>
	 *
	 * @example
	 * const axes = new eg.Axes({
	 *   "x": {
	 *      range: [0, 100]
	 *   },
	 *   "zoom": {
	 *      range: [50, 30]
	 *   }
	 * }).on("release", function(event) {
	 *   // event.depaPos
	 *   // event.destPos
	 *   // event.delta
	 *   // event.input
	 *   // event.inputEvent
	 *   // event.setTo
	 *   // event.isTrusted
	 *
	 *   // if you want to change the animation coordinates to move after the 'release' event.
	 *   event.setTo({x: 10}, 2000);
	 * });
	 */
	triggerRelease(param: AnimationParam) {
		const {roundPos, roundDepa} = this.getRoundPos(param.destPos, param.depaPos);
		param.destPos = roundPos;
		param.depaPos = roundDepa;
		param.setTo = this.createUserControll(param.destPos, param.duration);
		this.axes.trigger("release", param as OnRelease);
	}

	/**
	 * This event is fired when coordinate changes.
	 * @ko     
	 * @name eg.Axes#change
	 * @event
	 * @type {object}
	 * @property {Object.<string, number>} pos  The coordinate <ko></ko>
	 * @property {Object.<string, number>} delta  The movement variation of coordinate <ko> </ko>
	 * @property {Boolean} holding Indicates whether a user holds an element on the screen of the device.<ko>     </ko>
	 * @property {Object} input The instance of inputType where the event occurred. If the value is changed by animation, it returns 'null'.<ko>  inputType .      'null' .</ko>
	 * @property {Object} inputEvent The event object received from inputType. If the value is changed by animation, it returns 'null'.<ko>inputType    .      'null' .</ko>
	 * @property {set} set Specifies the coordinates to move after the event. It works when the holding value is true <ko>    . holding  true  .</ko>
	 * @property {Boolean} isTrusted Returns true if an event was generated by the user action, or false if it was caused by a script or API call <ko>     true,  API    false .</ko>
	 *
	 * @example
	 * const axes = new eg.Axes({
	 *   "x": {
	 *      range: [0, 100]
	 *   },
	 *   "zoom": {
	 *      range: [50, 30]
	 *   }
	 * }).on("change", function(event) {
	 *   // event.pos
	 *   // event.delta
	 *   // event.input
	 *   // event.inputEvent
	 *   // event.holding
	 *   // event.set
	 *   // event.isTrusted
	 *
	 *   // if you want to change the coordinates to move after the 'change' event.
	 *   // it works when the holding value of the change event is true.
	 *   event.holding && event.set({x: 10});
	 * });
	 */
	triggerChange(pos: Axis, isAccurate?: boolean, depaPos?: Axis, option?: ChangeEventOption, holding: boolean = false) {
		const am = this.am;
		const axm = am.axm;
		const eventInfo = am.getEventInfo();
		const {roundPos, roundDepa} = this.getRoundPos(pos, depaPos);
		const moveTo = axm.moveTo(roundPos, roundDepa);
		const inputEvent = option && option.event || eventInfo && eventInfo.event || null;
		const param = {
			pos: moveTo.pos,
			delta: moveTo.delta,
			holding,
			inputEvent,
			isTrusted: !!inputEvent,
			input: option && option.input || eventInfo && eventInfo.input || null,
			set: inputEvent ? this.createUserControll(moveTo.pos) : () => { },
		};
		const result = this.axes.trigger("change", param);

		inputEvent && axm.set(param.set()["destPos"]);

		return result;
	}

	/**
	 * This event is fired when animation starts.
	 * @ko    .
	 * @name eg.Axes#animationStart
	 * @event
	 * @type {object}
	 * @property {Object.<string, number>} depaPos The coordinates when animation starts<ko>     </ko>
	 * @property {Object.<string, number>} destPos The coordinates to move to. If you change this value, you can run the animation<ko> .     </ko>
	 * @property {Object.<string, number>} delta  The movement variation of coordinate <ko> </ko>
	 * @property {Number} duration Duration of the animation (unit: ms). If you change this value, you can control the animation duration time.<ko>  (: ms).       .</ko>
	 * @property {Object} input The instance of inputType where the event occurred. If the value is changed by animation, it returns 'null'.<ko>  inputType .      'null' .</ko>
	 * @property {Object} inputEvent The event object received from inputType <ko>inputType    </ko>
	 * @property {setTo} setTo Specifies the animation coordinates to move after the event <ko>     </ko>
	 * @property {Boolean} isTrusted Returns true if an event was generated by the user action, or false if it was caused by a script or API call <ko>     true,  API    false .</ko>
	 *
	 * @example
	 * const axes = new eg.Axes({
	 *   "x": {
	 *      range: [0, 100]
	 *   },
	 *   "zoom": {
	 *      range: [50, 30]
	 *   }
	 * }).on("release", function(event) {
	 *   // event.depaPos
	 *   // event.destPos
	 *   // event.delta
	 *   // event.input
	 *   // event.inputEvent
	 *   // event.setTo
	 *   // event.isTrusted
	 *
	 *   // if you want to change the animation coordinates to move after the 'animationStart' event.
	 *   event.setTo({x: 10}, 2000);
	 * });
	 */
	triggerAnimationStart(param: AnimationParam): boolean {
		const {roundPos, roundDepa} = this.getRoundPos(param.destPos, param.depaPos);
		param.destPos = roundPos;
		param.depaPos = roundDepa;
		param.setTo = this.createUserControll(param.destPos, param.duration);
		return this.axes.trigger("animationStart", param as OnAnimationStart);
	}

	/**
	 * This event is fired when animation ends.
	 * @ko    .
	 * @name eg.Axes#animationEnd
	 * @event
	 * @type {object}
	 * @property {Boolean} isTrusted Returns true if an event was generated by the user action, or false if it was caused by a script or API call <ko>     true,  API    false .</ko>
	 *
	 * @example
	 * const axes = new eg.Axes({
	 *   "x": {
	 *      range: [0, 100]
	 *   },
	 *   "zoom": {
	 *      range: [50, 30]
	 *   }
	 * }).on("animationEnd", function(event) {
	 *   // event.isTrusted
	 * });
	 */
	triggerAnimationEnd(isTrusted: boolean = false) {
		this.axes.trigger("animationEnd", {
			isTrusted,
		});
	}
	/**
	 * This event is fired when all actions have been completed.
	 * @ko    .
	 * @name eg.Axes#finish
	 * @event
	 * @type {object}
	 * @property {Boolean} isTrusted Returns true if an event was generated by the user action, or false if it was caused by a script or API call <ko>     true,  API    false .</ko>
	 *
	 * @example
	 * const axes = new eg.Axes({
	 *   "x": {
	 *      range: [0, 100]
	 *   },
	 *   "zoom": {
	 *      range: [50, 30]
	 *   }
	 * }).on("finish", function(event) {
	 *   // event.isTrusted
	 * });
	 */
	triggerFinish(isTrusted: boolean = false) {
		this.axes.trigger("finish", {
			isTrusted,
		});
	}
	private createUserControll(pos: Axis, duration: number = 0) {
		// to controll
		const userControl = {
			destPos: { ...pos },
			duration,
		};
		return (toPos?: Axis, userDuration?: number): { destPos: Axis, duration: number } => {
			toPos && (userControl.destPos = { ...toPos });
			(userDuration !== undefined) && (userControl.duration = userDuration);
			return userControl;
		};
	}

	setAnimationManager(am: AnimationManager) {
		this.am = am;
	}

	destroy() {
		this.axes.off();
	}

	private getRoundPos(pos: Axis, depaPos?: Axis) {
		// round value if round exist
		const roundUnit = this.axes.options.round;

		// if (round == null) {
		// 	return {pos, depaPos}; // undefined, undefined
		// }
		return {
			roundPos: roundNumbers(pos, roundUnit),
			roundDepa: roundNumbers(depaPos, roundUnit),
		};
	}
}
